/* src/components/verify-email-popup/verify-email-popup.css */
.backdrop {
  position: fixed;
  inset: 0;
  background: var(--modal-backdrop-color);
  -webkit-backdrop-filter: blur(var(--modal-backdrop-blur));
  backdrop-filter: blur(var(--modal-backdrop-blur));
  z-index: 900;
}
.popup-container.desktop {
  position: fixed;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 950;
}
.popup-container.desktop .popup-content {
  position: relative;
  align-items: center;
  width: 30%;
  height: auto;
  background: var(--modal-surface);
  border-radius: var(--modal-radius);
  padding: 40px;
  border: var(--modal-border);
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.popup-container.tablet {
  position: fixed;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 950;
}
.popup-container.tablet .popup-content {
  position: relative;
  align-items: center;
  width: 40%;
  height: auto;
  background: var(--modal-surface);
  border-radius: var(--modal-radius);
  padding: 40px;
  border: var(--modal-border);
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.backdrop.mobile {
  display: none;
}
.popup-container.mobile {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.popup-container.mobile .popup-content {
  position: fixed;
  background: var(--modal-surface);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1.5rem;
  height: 100%;
  width: 100%;
  border-radius: 0;
  overflow-y: auto;
  gap: 24px;
}
.close-btn {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  padding: 4px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.close-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}
.close {
  position: absolute;
  top: 20px;
  right: 20px;
}
.header {
  text-align: center;
}
.error {
  color: var(--color-error);
}
.success {
  color: #14c972;
}
/*# sourceMappingURL=verify-email-popup.css.map */
